.us_equities__animation {
  animation-name: floating_us_equities;
  animation-duration: 10s;
  animation-iteration-count: infinite;
}
.emerging_market_equities__animation {
    animation-name: floating_emerging_market_equities;
    animation-duration: 9s;
    animation-iteration-count: infinite;
}
.fixed_income__animation {
    animation-name: floating_fixed_income;
    animation-duration: 10s;
    animation-iteration-count: infinite;
}
.intl_equities__animation {
    animation-name: floating_intl_equities;
    animation-duration: 10s;
    animation-iteration-count: infinite;
}
.venture_capital__animation {
    animation-name: floating_venture_capital;
    animation-duration: 9s;
    animation-iteration-count: infinite;
}
.commodities__animation {
  animation-name: floating_commodities;
  animation-duration: 10s;
  animation-iteration-count: infinite;
}
.real_estate__animation {
    animation-name: floating_real_estate;
    animation-duration: 10s;
    animation-iteration-count: infinite;
}
.bitcoin__animation {
    animation-name: floating_bitcoin;
    animation-duration: 10s;
    animation-iteration-count: infinite;
}


@keyframes floating {
  0% {
    transform: translate(0%, 0%);
  }

  12.5% {
    transform: translate(.5%, 1.5%);
  }

  25% {
    transform: translate(1%, 3%);
  }

  50% {
    transform: translate(0%, 4%);
  }

  62.5% {
    transform: translate(-.5%, 3%);
  }

  75% {
    transform: translate(-1%, 1%);
  }

  100% {
    transform: translate(0%, 0%);
  }
}

@keyframes floating_bitcoin {
  0% {
    transform: translate(0%, 0%);
  }

  12.5% {
    transform: translate(1%, 3%);
  }

  25% {
    transform: translate(2.5%, 7%);
  }

  50% {
    transform: translate(0%, 15%);
  }

  62.5% {
    transform: translate(-1.5%, 7%);
  }

  75% {
    transform: translate(-2.5%, 3%);
  }

  100% {
    transform: translate(0%, 0%);
  }
}

