.appBar{
  background-color:#f3faf8!important;
  padding: 15px 15px;
  position: fixed;
  width: 100%;
  z-index: 6;
}
.nav-items{
  display: flex;
  width: 100%;
  justify-content: space-between;
  align-items: center;
}
.items{
  display: flex;
  padding: 0px;
  list-style-type: none;
  margin-bottom: 0px;
  align-items: center;
  li{
    color: #001738!important;
    margin-left: 15px;
  }
}
.login-action{
  display: flex;
  padding: 0px;
  list-style-type: none;
  margin-bottom: 0px;
  align-items: center;
  @media screen and (max-width: 560px) {
    flex-direction: column;
  }
  .signUp{
    background-color: #1abc9c;
    color: #fff!important;
    margin-left: 15px;
    font-weight: 600;
    height: 40px;
    font-size: 13px;
    width: 120px;
    border-radius: 6px;
    display: flex;
    align-items: center;
    justify-content: center;
    @media screen and (max-width: 560px) {
      margin-left: unset;
    }
      a{
      color: #fff!important;

    }
  }

  .login{
    color: #001738;
    font-size: 14px;
    a{
      color: #001738;

    }
  }
}
.introduction {
  z-index: 1;
  text-align: right;
  background-color: #f3faf8;
  height: 85vh;
  padding-top: 150px;
  .site-name{
    color: #001738;
    font-size: 40px;
    font-weight: 700;
  }

  .site-des{
    color: #001738;
    font-size: 24px;
    line-height: 32px;
    margin-top: 30px;
    margin-bottom: 30px;
  }

  .btn-green {
    border-radius: 3px;
    color: white;
    font-size: 14px;
    font-weight: 500;
    border: 1px solid #1abc9c;
    background-color: #1abc9c;
    transition: 0.2s;
  }

  .btn-fixed {
    padding: 10px 5px;
    display: inline-block;
    text-align: center;
    white-space: nowrap;
    width: 184px;
  }
}


.objective{
  background-color: white;
  .container{
    padding: 100px 0px;

    @media screen and (max-width: 560px) {
      padding: 40px 15px;
    }
  }

  .objective__intro {
    margin-bottom: 50px;

    h1{
      color: #001738;
    }

    .objective__title__description {
      width: 90%;
      line-height: 30px;
      margin-top: 18px;
      margin-bottom: 27px;
      font-size: 16px;
      color: #001738;
    }

    .text-link-green {
      color: #1abc9c;
      text-decoration: none;
      font-weight: 400;
    }

    .text-14 {
      font-size: 14px;
    }
  }


  .objective__block{
    padding-left: 5px;
    padding-right: 5px;
    height: 330px;
    @media screen and (max-width: 560px) {
      padding-left: 15px;
      padding-right: 15px;
    }
    .objective__content {
      position: relative;
      box-shadow: 0px 2px 15px 0px rgba(200,199,204,0.21);
      min-height: 250px;
      height: 100%;
      padding: 20px;
      border: 1px solid #f5f5f5;
      margin: 0 auto;
    }

    .box-shadow-animate {
      box-shadow: 0 10px 10px rgba(0,0,0,0.08);
      transition: all 250ms cubic-bezier(0.02, 0.01, 0.47, 1);
    }

    .box-shadow-animate:hover {
      cursor: pointer;
      box-shadow: 0 20px 20px rgba(0,0,0,0.16);
      transform: translate(0, -2px);
      transition-delay: 0s !important;
    }
    .theme-white {
      background-color: #fff;
    }

    .objective__img-container {
      height: 125px;
      text-align: center;
      .objective__img {
        width: 104px;
      }
    }

    .objective__title {
      font-size: 14px;
      letter-spacing: -0.4px;
      margin-bottom: 15px;
      color: #001738;
    }

    .objective__title {
      white-space: nowrap;
    }

    .text-strong {
      font-weight: 700;
    }

    .objective__description {
      font-size: 14px;
      line-height: 23px;
      color: #001738;
    }

    .objective__link {
      letter-spacing: 0px;
      text-align: center;
      font-size: 12px;
      position: absolute;
      bottom: 7%;
      left: 50%;
      color: #707d8f;
      transform: translateX(-50%);
    }

    .text-medium {
      font-weight: 500;
    }
  }
}


.sectors{
  background-color: #1abc9c;
  color: #fff;
  height: 720px;
  padding-top: 80px;
  .third-part-title{
    font-size: 32px;
    line-height: 48px;

  }
  @media screen and (min-width: 992px) {

    .third-part-left {
      margin-top: 150px;
    }
  }

  @media screen and (max-width: 992px) {
    .reverse-third-part {
      flex-direction: column-reverse;
    }
    .third-part-left {
      margin-top: 30px;
      text-align: center;
    }
  }

  @media screen and (max-width: 560px) {
    .reverse-third-part {
      flex-direction: unset;
    }
  }

  @media screen and (min-width: 768px) {
    .third-part {
      background-color: #1ABC9C;
      height: 820px;
    }
  }
  @media screen and (max-width: 500px) {
    .third-part {
      background-color: #1ABC9C;
      height: 600px;
    }
  }
  @media screen and (max-width: 768px) {
    .third-part {
      background-color: #1ABC9C;
      height: 700px;
    }
  }

  @media only screen and (min-width: 992px) {

    .third-part {
      padding-bottom: 100px;
      padding-top: 100px;
    }
  }


  @media only screen and (max-width: 992px) {

    .third-part-left-body {
      text-align: justify;
      text-align-last: center;
    }
    .third-part-title{
      text-align: justify;
      text-align-last: center;

    }
  }


  .third-part-left p {
    color: #FFFFFF;

    text-align: justify;
  }
}

.app{
  background-color: white;
  padding-top: 70px;
  padding-bottom: 70px;
  .app__section__title {
    font-size: 32px;
    color: #001738;
  }

  .app__section__description {
    margin-top: 20px;
    margin-bottom: 20px;
    line-height: 30px;
    font-size: 16px;
    font-weight: 300;
    color: #001738;
  }

  .btn-invest-now {
    font-weight: 500;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    width: 120px;
    height: 40px;
    font-size: 13px;
    margin-left: 10px;

  }

  .btn-flat__green {
    background-color: #1abc9c;
    color: #fff;
  }
  .btn-download {
    height: 40px;
    display: inline-block;
  }
}

.trust{
  position: relative;
  background-color: #1abc9c;
  color: #fff;
  text-align: center;
  padding-top: 70px;
  padding-bottom: 70px;
  .trust__details {
    margin: 100px 0px 150px 0px;

    .trust__img{
      margin-bottom: 24px;
      width: 72px;
    }

    .trust__title {
      font-size: 20px;
      letter-spacing: -0.5px;
      margin-bottom: 12px;
    }

    .text-medium {
      font-weight: 500;
    }

    .trust__info {
      line-height: 27px;
      font-size: 14px;
    }

    .text-regular {
      font-weight: 400;
    }
  }
}



.footer {
  background-color: #001738;
  color: #FFFFFF;
  height: 200%;
}

.footer-description {
  text-align:center;
  padding-bottom: 40px;
  padding-top: 40px;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.footer p{
  line-height: 1.7;
  font-size: 15px;
  font-weight: 500;
}

.footer h6{
  line-height: 1.7;
  font-size: 20px;
  font-weight: 500;
}
.footer-description img {

  padding-bottom: 10px;
  width: 50px;
}

.app-store {
  width: 50%;
  text-align: left;
}
.app-store img{

  width: 135px;
  height: 40px;
}

.footer-below {
  padding-right: 40px;
  padding-left: 40px;
  padding-top: 10px;
  padding-bottom: 10px;
  background-color: #000F24;
}

.footer-below p {
  text-align: justify;
  text-align-last: center;
  font-size: x-small;
  color: #d4d4d4;
}
.social-icon {
  margin-right: 5px;
}
@media screen and (max-width: 1000px) {
  .social-icon{
    width: 20px !important;
  }
}
@media screen and (max-width: 1200px) {
}
